<template>
<div class="container-fluid" id="fourofour">
  <div class="row justify-content-center" id="logo-row">
    <img class="logo " src="../assets/logo.png"><br>
  </div>
  <div class="row justify-content-center align-items-center">
    <div class="d-flex col-md-4 justify-content-center">
      <div class="login-backdrop shadow register-spacing">
        <h3 class="errormsg">אופס, נראה שדף זה לא קיים</h3>
        <form>
          <button class="btn btn-block" @click="$router.back()">חזור חזרה</button>
          <button class="btn btn-block" @click="$router.push('/')">לדף הבית</button>
        </form>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'pagenotfound',
  data () {
    return {}
  }
}
</script>

<style scoped>
  #fourofour {
    margin-top: 0.2in;
  }
  #logo-row {
    padding-bottom: 7vh;
  }
  .logo {
    height: 20vh;
    width: 20vh;
  }
  .errormsg {
    margin-bottom: 7vh;
  }
  .login-backdrop{
    border-radius: 4px;
    background: #ffffff;
    padding: 20px 40px;
  }
</style>



