<template>
  <div class="container-fluid" id="registert">
    <!-- <div class="row">
        <div class="col-12 filler"></div>
    </div> -->
    <div class="row justify-content-center" id="logo-row">
      <img class="logo " src="../assets/logo.png"><br>
    </div>
    <div class="row justify-content-center align-items-center login-row">
      <div class="d-flex col-md-4 justify-content-center">
        <div class="login-backdrop shadow">
          <form>
            <div class="form-group">
              <i class="fas fa-id-card-alt"></i>
              <label for="id">תעודת זהות</label>
              <input type="text" class="form-control" id="id" v-model="id" aria-describedby="idHelp" placeholder="ת.ז."
                     oninvalid="this.setCustomValidity('הכנס תעודת זהות תקנית')" pattern="^[0-9]{9}$" required>
            </div>
            <div class="form-group">
              <i class="fa fa-user"></i>
              <label for="name">שם</label>
              <input type="text" class="form-control" id="name" v-model="name"  placeholder="שם"
                     oninvalid="this.setCustomValidity('רק אותיות ורווחים מותרים')" pattern="^(\p{L}|\s){4,45}$" required>
            </div>
            <div class="form-group">
              <i class="fas fa-book-open"></i>
              <label for="subject">נושא</label>
              <input type="text" class="form-control" id="subject" v-model="subject"  placeholder="נושא" pattern="^(.){2,45}$" required>
            </div>
            <div class="form-group">
              <i class="fa fa-lock"></i>
              <label for="password">סיסמה</label>
              <input type="password" class="form-control" id="password" v-model="password" placeholder="סיסמה" minlength="8" required>
            </div>
            <div class="form-group">
              <i class="fa fa-lock"></i>
              <label for="vpassword">וידוי סיסמה</label>
              <input type="password" @change="vpassword !== password ? $event.target.setCustomValidity('Passwords must match') : $event.target.setCustomValidity('')" class="form-control" id="vpassword" v-model="vpassword" placeholder="סיסמה" minlength="8" required>
            </div>
            <div class="form-group">
              <i class="fas fa-user-secret"></i>
              <label for="password">קוד סודי</label>
              <input type="password" class="form-control" id="secret" v-model="secret" placeholder="קוד סודי" required>
            </div>
            <div class="form-group form-check">
              <a id="inputError"> {{ errorMessage }} </a>
            </div>
            <button type="button" class="btn btn-secondary" @click="$router.back()">חזור</button>
            <button type="submit" class="btn btn-primary" @click="registerT(id, name, subject, password, secret)">הירשם</button>
          </form>
        </div>
      </div>
    </div>
    <!-- <div class="row">
        <div class="col-12 filler"></div>
    </div> -->
  </div>
</template>

<script>
export default {
  name: 'RegisterT',
  data () {
    return {
      id: '',
      name: '',
      subject: '',
      password: '',
      vpassword: '',
      secret: '',
      errorMessage: ''
    }
  },
  methods: {
    async registerT (id, name, subject, password, secret) {
      try {
        await this.$store.dispatch('registerT', {id, name, subject, password, secret})
        this.$router.push('/login')
      } catch (e) {
        if (e.message.includes('401')) {
          this.errorMessage = 'קוד סודי שגוי'
        }
      }
    }
  }
}
</script>

<style scoped>

  #registert {
    margin-top: 0.2in;
  }
  #logo-row {
    padding-bottom: 7vh;
  }
  .logo {
    height: 20vh;
    width: 20vh;
  }
  .login-backdrop{
    border-radius: 4px;
    background: #ffffff;
    padding: 20px 40px;
  }
  .form-group{
    text-align: right;
  }
  input.form-control:invalid {
    border-color: red;
  }
</style>



