import axios from 'axios'
const API = '/api'

async function postRequestNoErr (url, data) {
  try {
    return await axios.post(url, data, {withCredentials: true})
  } catch (e) {
    return null
  }
}

async function getRequestNoErr (url) {
  return axios.get(url, {withCredentials: true})
}

/*
 Post commands
*/

export async function authenticate (id, password, rememberMe) {
  return postRequestNoErr(API + '/user', {id, password, rememberMe})
}

export async function removeFirst () {
  return postRequestNoErr(API + '/removeFirst', {})
}

export async function logout () {
  return postRequestNoErr(API + '/logout', {})
}

export async function registerT (id, name, subject, password, secret) {
  return postRequestNoErr(API + '/registerteacher', {id, name, subject, password, secret})
}

export async function registerS (id, name, password, phone, email, cl) {
  return postRequestNoErr(API + '/register', {id, name, password, phone, email, cl})
}

export async function addClass (name) {
  return postRequestNoErr(API + '/addclass', {name})
}

export async function removeClass (id) {
  return postRequestNoErr(API + '/removeclass', {id})
}

/*
  Get commands
 */

export async function fetchUser () {
  return getRequestNoErr(API + '/user')
}

export async function fetchHeaders () {
  return getRequestNoErr(API + '/headers')
}

export async function fetchData () {
  return getRequestNoErr(API + '/studentdata')
}

export async function fetchClasses () {
  return getRequestNoErr(API + '/classes')
}

export async function fetchSubjects () {
  return getRequestNoErr(API + '/subjects')
}

export async function fetchMatches () {
  return getRequestNoErr(API + '/matches')
}



