<template>
  <div class="container-fluid" id="register">
    <!-- <div class="row">
        <div class="col-12 filler"></div>
    </div> -->
    <div class="row justify-content-center" id="logo-row">
      <img class="logo " src="../assets/logo.png"><br>
    </div>
    <div class="row justify-content-center align-items-center">
      <div class="d-flex col-md-4 justify-content-center">
        <div class="login-backdrop shadow register-spacing">
          <form>
            <button class="btn btn-block" @click="$router.push('/registerS')">הירשם כתלמיד</button>
            <button class="btn btn-block" @click="$router.push('/registerT')">הירשם כמורה</button>
            <button class="btn btn-info btn-block" @click="$router.push('/login')">התחבר</button>
          </form>
        </div>
      </div>
    </div>
    <!-- <div class="row">
        <div class="col-12 filler"></div>
    </div> -->
  </div>
</template>

<script>
export default {
  name: 'register'
}
</script>

<style scoped>

  #register {
    margin-top: 0.2in;
  }
  #logo-row {
    padding-bottom: 7vh;
  }
  .logo {
    height: 20vh;
    width: 20vh;
  }
  .login-backdrop{
    border-radius: 4px;
    background: #ffffff;
    padding: 20px 40px;
  }
</style>



