<template>
    <div id="admin">
      <div class="modal fade" id="addClassModal" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header text-center">
              <h4 class="modal-title w-100 font-weight-bold">הוסף כיתה</h4>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body mx-3">
              <div class="md-form mb-5">
                <label for="classname">שם הכיתה</label>
                <input type="text" id="classname" class="form-control" v-model="className" minlength="4" maxlength="45" required>
              </div>
              <div class="md-form">
                {{ errorMsg }}
              </div>
            </div>
            <div class="modal-footer d-flex justify-content-center">
              <button class="btn btn-default"  @click="addClass(className).then(toggleModal).catch(err=>{errorMsg='שם לא חוקי'})">הוסף</button>
            </div>
          </div>
        </div>
      </div>
      <div id="classesTable" class="divider container">
        <button class="btn mr-auto filter-btn" @click="filter = !filter">
          סינון  <i class="fas fa-filter"></i>
        </button>
        <form id="filterform" v-if="filter">
          <label for="textfil">הכנס טקסט:</label>
          <input type="text" id="textfil" v-model="filtertext">
        </form>
        <div class="responsive-table" >
          <table dir="rtl" class="table table-bordered table-condensed" >
            <thead>
            <tr>
              <th scope="col">כיתה</th>
              <th scope="col">מחק</th>
            </tr>
            </thead>
            <tr v-for="cl in classes" :key="cl.id" v-if="cl.name.toLowerCase().includes(filtertext.toLowerCase())">
              <td scope="col">{{ cl.name }}</td>
              <td scope="col" class="fit"><i class="fas fa-times pressable" @click="removeClass(cl.id)"></i></td>
            </tr>
            <tr v-if="classes.length === 0">
              <td colspan="2">אין כיתות</td>
            </tr>
          </table>
          <button class="btn mr-auto" data-toggle="modal" data-target="#addClassModal">
            הוסף <i class="fas fa-plus"></i>
          </button>
        </div>
      </div>
      <studentTable class="divider"></studentTable>
    </div>
</template>

<script>
import studentTable from './studentTable'

export default {
  name: 'Admin',
  components: {
    studentTable
  },
  computed: {
    classes () {
      return this.$store.state.general.classes
    }
  },
  methods: {
    async addClass (className) {
      return this.$store.dispatch('addClass', className)
    },
    async removeClass (classID) {
      return this.$store.dispatch('removeClass', classID)
    },
    toggleModal () {
      $('#addClassModal').modal('toggle')
    }
  },
  created () {
    Promise.resolve(this.$store.dispatch('fetchClasses')).catch(() => {
      this.$router.push('/login')
    })
  },
  data () {
    return {
      filter: false,
      filtertext: '',
      className: '',
      errorMsg: ''
    }
  }
}
</script>

<style scoped>
.divider + .divider {
  border-top: #4793aa solid 2px;
  padding-top: 10px;
  margin-top: 10px;
}
.fit {
  width: 1px;
  border-right: transparent;
}
.pressable:hover {
  cursor: pointer;
}
</style>



