import Vue from 'vue'
import Router from 'vue-router'
import MainPage from '@/components/MainPage'
import Login from '@/components/Login'
import BootStrapVue from 'bootstrap-vue'
import store from '@/store'
import config from '@/config'
import studentTable from '@/components/studentTable'
import pagenotfound from '@/components/PageNotFound'
import student from '@/components/Student'
import register from '@/components/Register'
import registerT from '@/components/RegisterT'
import registerS from '@/components/RegisterS'
import Admin from '@/components/Admin'

Vue.use(Router)
Vue.use(BootStrapVue)

const router = new Router({
  routes: [{
    path: '/',
    name: 'MainPage',
    component: MainPage,
    beforeEnter (to, from, next) {
      if (store.state.auth.user.type === 0) {
        next('/student')
      } else if (store.state.auth.user.type === 1) {
        next('/teacher')
      } else {
        next('/admin')
      }
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/teacher',
    name: 'teacher',
    component: studentTable,
    beforeEnter (to, from, next) {
      if (!(store.getters.isTeacher)) {
        next('/')
      }
      next()
    }
  },
  {
    path: '/student',
    name: 'student',
    component: student,
    beforeEnter (to, from, next) {
      if (!(store.getters.isStudent)) {
        next('/')
      }
      next()
    }
  },
  {
    path: '/register',
    name: 'register',
    component: register
  },
  {
    path: '/registerT',
    name: 'registerT',
    component: registerT
  },
  {
    path: '/registerS',
    name: 'registerS',
    component: registerS
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin,
    beforeEnter (to, from, next) {
      if (!(store.getters.isAdmin)) {
        next('/')
      }
      next()
    }
  },
  {
    path: '*',
    name: 'pagenotfound',
    component: pagenotfound
  }]
})

router.beforeEach((to, from, next) => {
  if (!store.getters.isAuthenticated) {
    store.dispatch('fetchUser').then(() => {
      if (config.need_auth.includes(to.path)) {
        next()
      }
      next('/')
    }).catch(() => {
      if (!store.getters.isAuthenticated && config.need_auth.includes(to.path)) {
        return next('/login')
      }
      return next()
    })
  } else {
    if (!store.getters.isAuthenticated && config.need_auth.includes(to.path)) {
      return next('/login')
    }
    return next()
  }
})

export default router



