import * as connector from '@/api/connector' // api backend connector

const state = {
  classes: [],
  subjects: [],
  matches: []
}

const mutations = {
  fetchClasses (state, data) {
    state.classes = data.classes
  },
  fetchMatches (state, data) {
    state.matches = data.matches
  }
}

const actions = {
  async fetchClasses ({commit}) {
    const response = await connector.fetchClasses()
    commit('fetchClasses', response.data)
  },
  async fetchMatches ({commit}) {
    const response = await connector.fetchMatches()
    commit('fetchMatches', response.data)
  },
  async addClass ({dispatch}, name) {
    await connector.addClass(name)
    dispatch('fetchClasses')
  },
  async removeClass ({dispatch}, id) {
    await connector.removeClass(id)
    dispatch('fetchClasses')
  }
}

export default {
  state,
  mutations,
  actions
}



