import * as connector from '@/api/connector' // api backend connector

const state = {
  headers: [],
  data: []
}

const getters = {}

const mutations = {
  fetchHeaders (state, data) {
    state.headers = data.headers
  },
  fetchData (state, data) {
    state.data = data.data
  }
}

const actions = {
  async fetchHeaders ({ commit, rootState }) {
    const response = await connector.fetchHeaders()
    commit('fetchHeaders', response.data)
  },
  async fetchData ({ commit }) {
    const response = await connector.fetchData()
    commit('fetchData', response.data)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



