import * as connector from '@/api/connector' // api backend connector

const state = {
  user: null
}

const getters = {
  isAuthenticated: state => {
    return state.user !== null
  },
  isAdmin: state => {
    if (state.user == null) {
      return false
    }
    return state.user.type === 2
  },
  isTeacher: state => {
    if (state.user == null) {
      return false
    }
    return state.user.type === 1
  },
  isStudent: state => {
    if (state.user == null) {
      return false
    }
    return state.user.type === 0
  }
}

const mutations = {
  authenticate (state, data) {
    state.user = data
  },
  logout (state) {
    state.user = null
  },
  removeFirst () {
    if (!state.user) {
      return
    }
    state.user.firstLogin = 0
  }
}

const actions = {
  async authenticate ({ commit }, data) {
    // API will return the correct type, the id, the name and if this is the firstLogin
    /*
    types:
    0 - student
    1 - teacher
    2 - administrator
    */
    const response = await connector.authenticate(data.id, data.password, data.rememberMe)
    if (!response) {
      state.user = null
      return
    }
    const userinfo = response.data
    commit('authenticate', userinfo)
  },
  async logout ({ commit }) {
    await connector.logout()
    commit('logout')
  },
  async removeFirst ({ commit }) {
    await connector.removeFirst()
    commit('removeFirst')
  },
  async registerT (a, data) {
    return connector.registerT(data.id, data.name, data.subject, data.password, data.secret)
  },
  async registerS (a, data) {
    return connector.registerS(data.id, data.name, data.password, data.phone, data.email, data.cl)
  },
  async fetchUser ({commit}) {
    const response = await connector.fetchUser()
    commit('authenticate', response.data)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



